.at-modal-footer {
  border-top: 0.02133rem #E5E5E5 solid;
}

.at-modal-footer .at-modal-action {
  display: -webkit-flex;
  display: flex;
}

.at-modal-footer .at-modal-action > button {
  -webkit-flex: auto;
          flex: auto;
  -webkit-box-flex: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
  min-width: 3.41333rem;
  height: 1.87733rem;
  font-size: 0.59733rem;
  line-height: 1.83467rem;
  outline: 0;
  border: 0;
  border-radius: 0;
  background-color: white;
  margin-top: 0;
}

.at-modal-footer .at-modal-action > button:active {
  background-color: #F0F0F0;
}

.at-modal-footer .at-modal-action > button::after {
  border: 0;
  border-radius: 0;
}

.at-modal-footer .at-modal-action > button:not(:first-child) {
  border-left: 0.02133rem #E5E5E5 solid;
}

.at-modal-footer .at-modal-action > button:first-child:last-child {
  color: #6190E8;
}

.at-modal-footer--simple .at-modal-action > button:last-child:nth-child(2) {
  color: #6190E8;
}

